.page-header {
  padding-top: 11px;

  @media (min-width: @tablet-width) {
    padding-top: 25px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 53px;
  }
}

.page-header--index {
  @media (min-width: @desktop-width) {
    margin-bottom: 80px;
    min-height: 696px;

    background-image:
      url("../img/index-background-desktop@1x.jpg"),
      linear-gradient(to right, white 50%, #65a93c 50%);
    background-repeat: no-repeat;
    background-position: left calc(50% + 360px) top 0, left top;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image:
        url("../img/index-background-desktop@2x.jpg"),
        linear-gradient(to right, white 50%, #65a93c 50%);
      background-size: 720px auto, 100% 100%;
    }
  }
}
