.collaboration {
  position: relative;
  margin-bottom: 36px;
  padding-top: 14px;
  width: 100%;
  min-height: 476px;

  background-color: @bc-gray;

  @media (min-width: @tablet-width) {
    margin-bottom: 39px;
    padding-top: 34px;
    min-height: 600px;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    min-height: 400px;
    padding-top: 95px;
  }
}

.collaboration--index {
  background-color: @bc-white;
}

.collaboration__contacts {
  position: relative;
  z-index: 1;

  display: flex;
  margin-bottom: 13px;

  @media (min-width: @tablet-width) {
    justify-content: space-between;
    padding-top: 15px;
    margin-bottom: 13px;
  }

  @media (min-width: @desktop-width) {
    justify-content: space-around;

    padding-bottom: 14px;
    padding-top: 46px;
    width: 570px;

    background-color: @bc-white;
  }
}

.collaboration__title {
  font-family: @font-title;
  .set-font-size(16px, 20px);
  text-transform: uppercase;
  font-weight: 400;
  color: @color-title;

  @media (min-width: @tablet-width) {
    .set-font-size(20px, 25px);
    width: 30%;
    padding-left: 41px;
    margin-bottom: 40px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 31px;
    margin-right: 17px;
  }
}

.collaboration__address {
  .set-font-size(14px, 20px);
  color: @color-text;

  @media (min-width: @tablet-width) {
    padding-right: 39px;
    margin-bottom: 30px;
    .set-font-size(16px, 20px);
    text-align: right;
    width: 35%;
  }

  @media (min-width: @desktop-width) {
    text-align: left;
  }
}

.collaboration__street {
  padding-right: 4px;
  margin-bottom: 0;

  @media (min-width: @tablet-width) {
    margin-top: 13px;
    margin-bottom: 19px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 16px;
  }
}

.collaboration__city {
  margin-top: 0;

  @media (min-width: @tablet-width) {
    margin-right: 3px;
  }
}

.collaboration__map {
  height: 362px;
  width: 100%;

  background-image: url("../img/bg-map-mobile@1x.png");
  background-position: 50% -38px;
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    top: 252px;
    left: calc(50% - 30px);
    right: 0;
    width: 61px;
    height: 56px;
    background-image: url("../img/map-pin.png");
    background-size: 100%;
    background-repeat: no-repeat;
  }

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.png");
    background-size: 320px auto;
  }

  @media (min-width: @tablet-width) {
    height: 400px;

    background-image: url("../img/bg-map-tablet@1x.png");
    background-repeat: no-repeat;

    &::before {
      top: 345px;
      left: calc(50% - 62px);
      width: 124px;
      height: 123px;
      background-size: 100%;
      background-repeat: no-repeat;
    }


    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.png");
      background-size: 770px auto;
      background-position: 50%;
    }
  }

  @media (min-width: @desktop-width) {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;

    height: 400px;

    background-image: url("../img/bg-map-desktop@1x.png");
    background-repeat: no-repeat;

    &::before {
      top: 150px;
      left: calc(50% + 278px);
    }

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.png");
      background-size: 1440px auto;
      background-position-x: 50%;
    }
  }
}

.collaboration__iframe {
  position: relative;
  z-index: 3;
  background-color: @bc-gray;
  height: 100%;
  width: 100%;
  border: none;
}
